"use client";
import React, { useState } from "react";
import Link from "next/link";
import { useSession, signOut } from "next-auth/react";
import {
    Shield,
    LayoutGrid,
    User,
    Settings,
    LogOut,
    ChevronDown,
    Menu,
    X
} from "lucide-react";

const Navbar = () => {
    const { data: session } = useSession();
    const [isProfileOpen, setIsProfileOpen] = useState(false);
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    return (
        <nav className="bg-[#121212]/80 backdrop-blur-md border-b border-white/10 sticky top-0 z-50">
            <div className="container mx-auto px-6 h-16 flex items-center justify-between">

                {/* Left: Brand */}
                <div className="flex items-center gap-8">
                    <Link href="/" className="flex items-center gap-2 group">
                        <div className="bg-amber-600 p-1.5 rounded-lg group-hover:bg-amber-500 transition-colors">
                            <Shield className="text-white" size={20} />
                        </div>
                        <span className="text-xl font-bold text-white font-serif tracking-tight">
                            System<span className="text-amber-500">Auth</span>
                        </span>
                    </Link>

                    {/* Desktop Navigation Links */}
                    <div className="hidden md:flex items-center space-x-6">
                        <Link href="/" className="text-sm font-medium text-gray-400 hover:text-white transition-colors">
                            Home
                        </Link>
                        {session && (
                            <Link href="/admin/dashboard" className="text-sm font-medium text-amber-500 flex items-center gap-1.5 hover:text-amber-400 transition-colors">
                                <LayoutGrid size={14} />
                                Dashboard
                            </Link>
                        )}
                    </div>
                </div>

                {/* Right: Actions */}
                <div className="flex items-center gap-4">
                    {session ? (
                        <div className="relative">
                            {/* Profile Toggle Button */}
                            <button
                                onClick={() => setIsProfileOpen(!isProfileOpen)}
                                className="flex items-center gap-2 p-1 pr-2 rounded-full hover:bg-white/5 transition-all border border-transparent hover:border-white/10"
                            >
                                <div className="w-8 h-8 rounded-full bg-amber-600 flex items-center justify-center text-xs font-bold text-white shadow-lg shadow-amber-600/20">
                                    {session.user?.email?.charAt(0).toUpperCase()}
                                </div>
                                <span className="hidden sm:block text-sm text-gray-300 font-medium">
                                    Account
                                </span>
                                <ChevronDown size={14} className={`text-gray-500 transition-transform duration-200 ${isProfileOpen ? 'rotate-180' : ''}`} />
                            </button>

                            {/* Dropdown Menu */}
                            {isProfileOpen && (
                                <>
                                    <div className="fixed inset-0 z-10" onClick={() => setIsProfileOpen(false)}></div>
                                    <div className="absolute right-0 mt-3 w-60 bg-[#1c1c1c] border border-white/10 rounded-xl shadow-2xl z-20 py-2 overflow-hidden animate-in fade-in zoom-in duration-200">
                                        <div className="px-4 py-3 border-b border-white/5">
                                            <p className="text-[10px] text-gray-500 font-mono uppercase tracking-widest mb-1">Identity Verified</p>
                                            <p className="text-sm text-white font-medium truncate">{session.user?.email}</p>
                                        </div>

                                        <div className="p-1">
                                            <Link
                                                href="/admin/profile"
                                                className="flex items-center gap-3 px-3 py-2 text-sm text-gray-300 hover:bg-amber-600 hover:text-white rounded-lg transition-all"
                                                onClick={() => setIsProfileOpen(false)}
                                            >
                                                <User size={16} /> Update Profile
                                            </Link>
                                            <Link
                                                href="/admin/settings"
                                                className="flex items-center gap-3 px-3 py-2 text-sm text-gray-300 hover:bg-white/5 rounded-lg transition-all"
                                                onClick={() => setIsProfileOpen(false)}
                                            >
                                                <Settings size={16} /> System Settings
                                            </Link>
                                        </div>

                                        <div className="p-1 border-t border-white/5 mt-1">
                                            <button
                                                onClick={() => signOut({ callbackUrl: '/', redirect: true })}
                                                className="w-full flex items-center gap-3 px-3 py-2 text-sm text-red-400 hover:bg-red-500/10 rounded-lg"
                                            >
                                                <LogOut size={16} /> Sign Out Session
                                            </button>
                                        </div>
                                    </div>
                                </>
                            )}
                        </div>
                    ) : (
                        <Link
                            href="/login"
                            className="px-6 py-2 bg-amber-600 text-white rounded-full hover:bg-amber-500 transition-all text-sm font-bold shadow-lg shadow-amber-600/20"
                        >
                            Secure Login
                        </Link>
                    )}

                    {/* Mobile Menu Toggle */}
                    <button className="md:hidden text-gray-400" onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}>
                        {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
                    </button>
                </div>
            </div>

            {/* Mobile Menu Dropdown */}
            {isMobileMenuOpen && (
                <div className="md:hidden bg-[#1c1c1c] border-b border-white/10 px-6 py-4 space-y-4">
                    <Link href="/" className="block text-gray-300">Home</Link>
                    {session && <Link href="/admin/dashboard" className="block text-amber-500">Dashboard</Link>}
                </div>
            )}
        </nav>
    );
};

export default Navbar;